.fileWarper{
    width: 100%;
    height: calc(100% - 5px);
    padding-top: 5px;
}
.file_li{
    display: inline-block;
    height: 40px;
    width: calc(50% - 30px);
    overflow: hidden;
    margin: 10px;
    border-radius: 5px;
    padding: 5px;
    background-color: rgba(0, 0, 0, 0.1);
    cursor: pointer;
    /* display: flex;
    justify-content: space-between;
    align-items: center; */
}
.file_li_active{
    background-color: rgba(0, 0, 0, 0.5)!important;
}
.file_li:hover{
    background-color: rgba(0, 0, 0, 0.2);
}
.dirImg,.fileImg{
    float: left;
    height: 40px;
    width: 40px;
    background-size: 40px 40px;
    margin-left: 20px;
}
.dirImg{
    background-image: url("../../icons/dir.svg");
}
.fileImg{
    background-image: url("../../icons/file.svg");
}
.dirs{
    width: calc(100% - 20px);
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    background-color: rgba(0, 0, 0, 0.2);
    margin: 0 10px;
    border-radius: 5px;
    cursor: pointer;
}
.tagName{
    padding: 10px;
    border-radius: 5px;
    height: 30px;
    line-height: 30px;
}
.filetag_left{
    float: left;
}
.filetag_right{
    float: right;
}
.tagName:hover{
    background-color: rgba(0, 0, 0, 0.2);
}
.fileName{
    float: left;
    height: 40px;
    width: 65%;
    line-height: 40px;
    font-size: 17px;
    margin-left: 20px;
}
.fileDownLoad{
    float: right;
    height: 30px;
    line-height: 30px;
    font-size: 17px;
    margin-right: 20px;
    padding: 5px;
    border-radius: 5px;
}
.fileDownLoad:hover{
    background-color: rgba(0, 0, 0, 0.3);
}

