.videoWraper{
    height: 90%;
    width: calc(100% - 20px);
    padding-left: 20px;
}

.tagNav{
    width: calc(100% - 20px);
    display: flex;
    flex-wrap: wrap;
    justify-content:left;
}
.tag {
    background-color: rgba(44, 59, 50, 0.2);
    color: rgba(255, 255, 255, 0.4);
    font-size: 15px;
    height: 20px;
    width: calc(10% - 20px);
    line-height: 20px;
    margin: 5px 10px 5px 0;
    padding: 5px;
    text-align: center;
    border-radius: 3px;
    cursor: pointer;
}
.tag:hover{
    background-color: rgba(48, 19, 19, 0.4);
}
.tagOn{
    background-color: rgba(44, 59, 50, 0.5)!important;
    color: rgba(255, 255, 255, 0.7);
}

.videoMain{
    width: 100%;
    height: 100%;
}
