.createdir{
    border: black 1px solid;
    position: fixed;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 150px;
    background-color: rgb(20, 105, 233);
    transform: translate(-50%, -50%);
    border-radius: 5px;
}

.create_inputouter{
    position: absolute;
    background-color: rgb(155, 191, 245);
    left: 50%;
    transform: translateX(-50%);
    width: 70%;
    height: 40px;
    border-radius: 5px;
    margin-top: 30px;

}
.create_inputinner{
    background-color: rgb(155, 191, 245);
    height: 30px;
    line-height: 30px;
    margin: 5px 0;
    width: calc(100% - 60px);
    margin-left: 20px;
    float: left;
}
.create_warn{
    height: 30px;
    line-height: 30px;
    margin: 5px 0;
    width: 40px;
    float: right;
    color: red;
}
.create_cancel{
    position: absolute;
    left: 0;
    bottom: 0;
    margin: 20px;
}
.create_sure{
    position: absolute;
    right: 0;
    bottom: 0;
    margin: 20px;
}
