.searchButton{
    float: right;
    width: 300px;
    height: 100%;
    font-size: 16px;
}
.searchButton_outer{
    width: 90%;
    height: 30px;
    margin: 5px;
    border-radius: 6px;
    background-color: rgb(155, 191, 245);
    display: flex;
    justify-content: space-between;
    padding: 5px;
    padding-left: 15px;
}
.searchButton_input{
    width: 90%;
    color: rgb(67, 18, 180);
    font-weight: bold;
    background-color: rgb(155, 191, 245);

}
.searchButton_icon{
    background-image: url("../../icons/search.png");
    background-size: 16px;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    width: 10%;
}
