.app{
    height: 100%;
    width: 100%;
    overflow: auto;
    font-size: 16px;
    background: linear-gradient(90deg, #5d87eb 20%,#fff)
}
.appWraper{
    margin: 0 auto;
    width: 1080px;
    height: 100%;

}
.appHeader{
    height: 50px;

}
.applogo{
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50px;
    width: 100px;
    background-image: url("./icons/logo.svg");
    background-repeat: no-repeat;
    background-size: 60px 30px;
    background-position: 20px 10px;
    /* background-color: rgb(84, 143, 219); */
}
.applogo>span{
    font-family: Origin, Helvetica Light, sans-serif;
    text-transform: uppercase;
    font-size: 1rem;
    color: rgb(255,242,181);
    background-image: linear-gradient(90deg, #b9cefd,#5d87eb);
    text-align: center;
    background-clip:text;
    -webkit-background-clip:text;
    -webkit-text-fill-color: transparent;
    letter-spacing: .5rem;
}

.appBody{
    height: calc(100% - 52px);
    border-top: rgba(57, 25, 240, 0.2) solid 2px;
}

.nav{
    float: left;
    width: 100px;
    display: flex;
    flex-direction: column;
    justify-content:flex-start;
    align-items: center;
    height: 100%;
}

.link{
    font-size: 17px;
    border-radius: 5px;
    background-color: rgba(255,255,255,0.2);
    color: rgba(0, 0, 0, 0.5);
    padding: 15px;
    margin: 5px;
    width: 60px;
}
.linkActive{
    background-color: rgba(255,255,255,0.8);
    color: rgba(0, 0, 0, 0.9);
}
.routesContent{
    float: right;
    width: calc(100% - 100px);
    height: 100%;
    overflow: auto;
}