#videotag{
    width: 100%;
    height: 200px;
}
.episode_swiper{
    flex-wrap: wrap;
}
.episode_slide{
    cursor: pointer;
    height: 50px;
    width: 60px;
    line-height: 50px;
    font-size: 15px;
    text-align: center;
    margin: 10px;
    background-color: rgba(47, 111, 141, 0.2);
}
.active_e_slide{
    background-color: rgba(47, 111, 141, 0.8); 
}

.season_button{
    background-color: rgba(47, 111, 141, 0.2);
    font-size: 17px;
    border: none;
    height: 35px;
    width: 130px;
    line-height: 35px;
    margin: 10px;
}
.active_s_button{
    background-color: rgba(47, 111, 141, 0.8); 
}