.msgbox{
    border: black 1px solid;
    position: fixed;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 150px;
    background-color: rgb(20, 105, 233);
    transform: translate(-50%, -50%);
    border-radius: 5px;
}
.msgbox_msg{
    position: absolute;
    background-color: rgb(155, 191, 245);
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 60%;
    height: 40px;
    line-height: 40px;
    border-radius: 5px;
    cursor: pointer;
    overflow: hidden;
    text-align: center;
}