.listWraper{
    width: 100%;
    height: 100%;
    overflow: auto;
    overflow-y: scroll;
    scrollbar-color: transparent transparent;
    scrollbar-track-color: transparent;
    -ms-scrollbar-track-color: transparent;
}
.list{
    width: calc(100% - 20px);
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    margin-bottom: 100px;
}
.list_item{
    width: calc(16.666% - 20px);
    display: inline-block;
    margin-right: 20px;
    margin-bottom: 10px;
    border-radius: 5px;
    overflow: hidden;
    cursor: pointer;
}
.coverImg{
    width: 100%;
    height: 180px;
}
.videoName{
    width: 100%;
    /* font-size: 14px; */
    height: 30px;
    line-height: 30px;
    font-size: 20px;
    background-color: #1e232c;
    color: white;
    text-align: center;
}