.dowmload_warper{
    width: 100%;
    margin: 20px 0;
}
.dowmload_header{
    font-size: 18px;
    font-weight: bold;
    /* color: white; */
    margin: 20px 0;
}
.download_s_wraper{
    margin: 20px 0;
    display: flex;
}
.download_season_button{
    background-color: rgba(47, 111, 141, 0.2);
    font-size: 17px;
    border: none;
    height: 35px;
    width: 130px;
    line-height: 35px;
    margin: 10px;
}
.download_active_s_button{
    background-color: rgba(47, 111, 141, 0.8); 
}
.download_ep{
    width: calc(100% - 40px);
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}
.download_ep:hover{
    background-color: rgba(14, 13, 13, 0.2);
    color: white;
}
/* .download_ep_name{
} */
.download_ep_button{
    cursor: pointer;
    padding: 5px;
}
.download_ep_button:hover{
    background-color: rgba(0, 0, 0, 0.5);
}