.upload{
    border: black 1px solid;
    position: fixed;
    top: 50%;
    left: 50%;
    width: 500px;
    height: 150px;
    background-color: rgb(20, 105, 233);
    transform: translate(-50%, -50%);
    border-radius: 5px;
}

.upload_outer{
    position: absolute;
    background-color: rgb(155, 191, 245);
    left: 50%;
    transform: translateX(-50%);
    width: 80%;
    height: 40px;
    border-radius: 5px;
    margin-top: 30px;
    cursor: pointer;
    overflow: hidden;
}
.upload_inner{
    display: none;

}
.upload_filename{
    background-color: rgb(155, 191, 245);
    height: 30px;
    line-height: 30px;
    margin: 5px 0;
    width: calc(100% - 150px);
    margin-left: 20px;
    float: left;
}
.upload_percent{
    width: 60px;
    float: left;
    height: 30px;
    line-height: 30px;
    margin: 5px 0;

}
.upload_button{
    width: 40px;
    float: right;
    height: 40px;
    line-height: 40px;
    text-align: center;
    /* margin: 5px 0; */

}
.upload_button:hover{
    background-color: rgba(0, 0, 0, 0.3);
}


.cancelButton{
    position: absolute;
    right: 0;
    top: 0;
    padding: 5px;
}
.cancelButton:hover{
    background-color: rgba(0, 0, 0, 0.3);
    cursor: pointer;
    border-radius: 5px;
}